@import url('https://fonts.googleapis.com/css2?family=Days+One&family=Lalezar&family=Righteous&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.section1{
    background: linear-gradient(180deg, #192A44 0%, #152340 30.73%, #20E0EA 55.21%, #1DA8B7 60.94%, #83359C 82.29%);
    background-repeat: no-repeat;
    height: 1024px;
}

.hero{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:50px;
    /* border:2px solid #a30606; */
}
.hero .hero_image{
    width: 600px;
height: 620px;
}
 .hero .hero_image img{
            width: 100%;
            height: 100%;
     }
.hero .hero_rec{
    /* display: flex;
    justify-content: center;
    align-items: center; */
   width: 421px;
height: 561px;
border-radius: 60px;
background: rgba(25, 42, 68, 0.70);
}
.hero .hero_rec_text{
    color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 100px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 4.5px;
}
@media (max-width:1200px){
  

.hero .hero_image {
    width: 460px;
height: 480px;
}
 .hero .hero_image img{
            width: 100%;
            height: 100%;
     }
 .hero .hero_rec{
    width: 326px;
height: 400px;
}
.hero .hero_rec_text{
    font-size: 80px;
}

}
@media (max-width:1024px){
   .hero .hero_image {
    width: 360px;
height: 380px;
}
 .hero .hero_image img{
            width: 100%;
            height: 100%;
     }
 .hero .hero_rec{
    width: 300px;
height: 350px;
}
 .hero .hero_rec_text{
    font-size: 70px;
}




}
@media (max-width:768px){
   .hero .hero_image {
    max-width: 310px;
height: 370px;
}
 .hero .hero_image img{
            width: 100%;
            height: 100%;
     }

.hero .hero_rec{
    display: flex;
    align-items: center;
    max-width: 200px;
    width:100%;
height: 320px;
}
.hero .hero_rec_text{
    font-size: 38px;
}
.hero{
    gap:10px;
    padding-left: 5px;
    padding-right: 5px;
}


}
@media (max-width:480px){
    .section1{
      /*   border:5px solid #a30606; */
        
    }
 
    .hero{
        display:flex;
        flex-direction: column;
        justify-content:space-evenly;
        padding-top:50px;
        height:100vh;
    /* border:10px solid #a30606; */
    gap:30px;
    }
    .hero .hero_image{
         max-width: 450px;
         
        height: 400px;
        order: 2;
     /* border: 2px solid #a30606; */}
     .hero .hero_image img{
            width: 100%;
            height: 100%;
     }
    .hero .hero_rec{
        order: 1;
        background: none;
        height: 300px;
         max-width: 300px;
         /* border: 5px solid #a30606; */
    }    
.hero .hero_rec_text{
    font-size: 50px;
}

}